@import url("https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@100;500&family=Open+Sans:wght@300&display=swap");
* {
  box-sizing: border-box;
}

body {
  font-family: "Barlow Semi Condensed", sans-serif;
  background-color: var(--color-bg);
  line-height: 1.5;
  font-size: 1.6rem;
  min-height: 100vh;
}

h1, h2, p {
  margin: 0;
}

:root {
  --color-white:#fff;
  --color-light-grey:#cfcfcf;
  --color-bg:#ecf2f8;
  --color-violet:#7541c8;
  --color-dark-grey:#19212e;
  --color-grey:#48556a;
}

.grid-container .avatar-img {
  border-radius: 50%;
  border: 2px solid #fff;
  margin-right: 0.625rem;
}

.grid-container .avatar {
  display: flex;
  align-items: center;
  font-size: 0.5em;
  color: var(--color-white);
}

.grid-container .avatar-name__name {
  padding-bottom: 0.3125rem;
}

.text-heading {
  color: var(--color-white);
  font-size: 1.1em;
  margin: 0.9375rem 0;
}
.text-heading--white-long {
  color: #000;
}
.text-heading--white-short {
  color: #000;
}

.text-detail {
  font-size: 0.7em;
  color: var(--color-light-grey);
}

.container {
  padding: 0 10%;
  width: 85%;
  max-width: 85%;
  margin: 0 auto;
}

h1 {
  font-size: 1.875rem;
  padding: 2% 0;
}

.grid-container__item--darkgrey, .grid-container__item--white_short, .grid-container__item--white_long, .grid-container__item--grey, .grid-container__item--violet {
  border-radius: 0.3rem;
  padding: 1.25rem;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 1.875rem;
  margin: 0 auto;
}
@media screen and (max-width: 1024px) {
  .grid-container {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr 1fr 1fr;
  }
}
@media screen and (max-width: 768px) {
  .grid-container {
    grid-template-columns: auto;
    grid-template-rows: auto;
  }
}
.grid-container__item {
  padding: 0.9375rem;
  font-size: 1.125rem;
}
.grid-container__item--violet {
  background-color: var(--color-violet);
  grid-column: 1/3;
  grid-row: span 1;
}
@media screen and (max-width: 1024px) {
  .grid-container__item--violet {
    grid-column: 1/3;
    grid-row: auto;
  }
}
@media screen and (max-width: 768px) {
  .grid-container__item--violet {
    grid-column: auto;
    grid-row: auto;
  }
}
.grid-container__item--grey {
  background-color: var(--color-grey);
  grid-column: 3/span 1;
  grid-row: span 1;
}
@media screen and (max-width: 1024px) {
  .grid-container__item--grey {
    grid-column: 1/2;
    grid-row: auto;
  }
}
@media screen and (max-width: 768px) {
  .grid-container__item--grey {
    grid-column: auto;
    grid-row: auto;
  }
}
.grid-container__item--white_long {
  background-color: var(--color-white);
  grid-column: 4/span 1;
  grid-row: 1/span 2;
}
.grid-container__item--white_long .avatar-name {
  color: #000;
}
.grid-container__item--white_long .text-detail {
  color: var(--color-grey);
}
@media screen and (max-width: 1024px) {
  .grid-container__item--white_long {
    grid-column: 2/3;
    grid-row: auto;
  }
}
@media screen and (max-width: 768px) {
  .grid-container__item--white_long {
    grid-column: auto;
    grid-row: auto;
  }
}
.grid-container__item--white_short {
  background-color: var(--color-white);
  grid-column: span 1;
  grid-row: span 1;
}
.grid-container__item--white_short .avatar-name {
  color: #000;
}
.grid-container__item--white_short .text-detail {
  color: var(--color-grey);
}
@media screen and (max-width: 1024px) {
  .grid-container__item--white_short {
    grid-column: 2/3;
    grid-row: auto;
  }
}
@media screen and (max-width: 768px) {
  .grid-container__item--white_short {
    grid-column: auto;
    grid-row: auto;
  }
}
.grid-container__item--darkgrey {
  background-color: var(--color-dark-grey);
  grid-column: 2/span 2;
  grid-row: span 1;
}
@media screen and (max-width: 1024px) {
  .grid-container__item--darkgrey {
    grid-column: 1/2;
    grid-row: 3/4;
  }
}
@media screen and (max-width: 768px) {
  .grid-container__item--darkgrey {
    grid-column: auto;
    grid-row: auto;
  }
}

.challenger {
  margin: 1.5625rem;
  text-align: center;
}